

#mario-chat{
    border-radius: 2px;
}
#mainPage{
    height: 100%;
    width: 100%;
}
#userArea{
    background-color: #aaa;
}

#chat-window{
    box-shadow: 1px 3px 5px rgba(0,0,0,0.05);

    background-image: url('/chatBackGround.png');
    border-radius: 0px 0px 30px 30px ;
    height: 28rem;
    overflow: auto;
}

#output p{
    text-align: center;

    padding: 3px 0px;
    margin: 0% 0px 5px 0px;
    color: #555;
}
#meassge1{
    width: 12rem;
}
#messageinput{
    margin-top: 12px;

    border-radius: 30px 0 0px 30px ;
}
#buttonChat{
    border-radius: 0px 30px 30px 0px ;
    height: 100%;
    background-image: url('/send.png');
    background-repeat: no-repeat;
    width: 50px;
    
    
}
#handle{
text-align: center;
text-transform: capitalize;
font-family: 'Arial';
text-shadow: 0px 0px #555;
}
#feedback p{

    position: absolute;
    color: rgb(255, 0, 0);
    padding: 14px 0px;
    margin: 10px 20px;
    z-index: 111;
}
#textareaChat{
    margin-top: 12px;
}

#body-message{
    text-align: left;
position: absolute;
display: inline;
margin: 2% 2% 0 0;
max-width: 80%;
}
#body-message small{
font-size:small;
font-weight: 700;

}
 #textMessage{
    word-break: break-all;
    word-wrap: break-word;
    
    word-spacing: 2px;
    
} 
#message{

    padding:4px;
 background-color: #ddd;
border-radius:  50px ;

}

.modal-dialog{
    z-index: 11111;
}

.accordion{
    width: 53px;
}
.imgLeg {
     cursor: pointer;
    margin: 0 10px;
}
.imageAddFri:hover{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    text-align: center;
}

.toast-body{
    white-space: pre-line;
    
}
.textLeft{
    text-align: left;
}
.textRight{
    text-align: right;
}

#chat-window::-webkit-scrollbar-track{
    border:1px gray solid;
    border-radius:10px;
    } 
#CWE{
    background-color: #e5ebee50;
}

body{
    font-family: 'Nunito';
}

h2{
    font-size: 18px;
    padding: 10px 20px;
    color: #575ed8;
}
label{
    box-sizing: border-box;
    display: block;
    padding: 10px 20px;
}

input{
    padding: 10px 20px;
    box-sizing: border-box;
    background: #eee;
    border: 0;
    display: block;
    width: 100%;
    background: #fff;
    border-bottom: 1px solid #eee;
    font-family: Nunito;
    font-size: 16px;
}

/* video class */

#myVideoTag{
    position: absolute;
    width: 10%;
}